<template>
  <div>
    <div class="box">
      <h1>Wishes</h1>
      <h5>时间就是这样：夜里想着心事，第二天的闹铃就响了，下几次雨，夏天也结束了。等反应过来的时候，一年已经快要过去了。</h5>
    </div>
    <!-------------------------------------------------->
<!--    <div style="height: 860px">-->
      <div @click="page(t[0])" class="box" v-for="(t,index) in this.urls.slice(this.pages*2-2,this.pages*2)" :key="index">
        <div class="exhibition">
          <div class="img" :style="{'background':'url(\''+t[3]+'\') 100% 35% no-repeat'}"></div>
          <div class="txt">
            <h2>
              <i class="el-icon-edit"></i>{{ t[1] }}
            </h2>
            <h4>{{ t[2] }}</h4>
          </div>
        </div>
      </div>
<!--    </div>-->
    <!-------------------------------------------------->
    <el-pagination
        background
        layout="prev, pager, next"
        :total="this.urls.length"
        :page-size="2"
        style="margin: 10px auto;display: flex;justify-content: center;"
        @next-click="nextClick"
        @prev-click="prevClick"
        @current-change="currentChange"
    >
    </el-pagination>
  </div>
</template>

<script>
import indexjs from "./js/indexWeb.js"
export default {
  name: "IndexWeb",
  data() {
    return {
      currentDate: new Date(),
      pages:1,
      urls: {}
    };
  },
  created() {
    this.urls=indexjs.urls
  },
  methods:{
    page(e){
      const loading = this.$loading({
        lock: true,
        text: '拼命加载中，请稍后......',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)',
      });
      setTimeout(() => {
        loading.close();
        this.$router.push({
          path:'/page',
          query: {
            context: e
          }})
      }, Math.ceil(Math.random()*200)+200);
    },
    nextClick(e){
      const loading = this.$loading({
        lock: true,
        text: '拼命加载中，请稍后......',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      setTimeout(() => {
        loading.close();
        this.pages=e
      }, Math.ceil(Math.random()*200)+200);
    },
    prevClick(e){
      const loading = this.$loading({
        lock: true,
        text: '拼命加载中，请稍后......',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      setTimeout(() => {
        loading.close();
        this.pages=e
      }, Math.ceil(Math.random()*200)+200);
    },
    currentChange(e){
      const loading = this.$loading({
        lock: true,
        text: '拼命加载中，请稍后......',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      setTimeout(() => {
        loading.close();
        this.pages=e
      }, Math.ceil(Math.random()*200)+200);
    }
  }
}
</script>

<style scoped>

.box{
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);display: flex;
  flex-direction: column;
}
.img{
  width: 100%;
  height: 270px;
}
.txt{
  width: 90%;
  height: 100px;
  margin: 0 auto 10px;
  background: #fff;
}
.exhibition{
  width: 100%;
  margin: 10px auto;
}
h1{
  margin: 10px 0 0 10px;
  color: #444;
}
h2{
  color:#666;
  margin-bottom: 0;
}
h4{
  color:#666;
  margin-top: 5px;
  text-indent:2em;

}
h5{
  margin: 5px auto 10px;
  color: #aaaaaa;
}

.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}
</style>